import Store from './store'
import coinAbi from './tokenAbi'
import Global from './global'

const Web3 = require('web3')
const Eth = require('ethjs-query')
const EthContract = require('ethjs-contract')

const Tx = require('ethereumjs-tx')
const web3js = new Web3(new Web3.providers.HttpProvider(Store.getters.getMainInfura))

/**
 * rinkeby
 * metamask infura
* */
// 
function checkExpress () {
  let ok = 0
  if (navigator.userAgent.indexOf('Firefox') >= 0) {
    ok = 1
  } else if (navigator.userAgent.indexOf('Opera') >= 0) {
    ok = 1
  } else if (navigator.userAgent.indexOf('Chrome') >= 0) {
    ok = 1
  } else if (navigator.userAgent.indexOf('Brave') >= 0) {
    ok = 1
  }
  console.log('express:' + ok)
  if (ok === 0) {
    // if (confirm('360,Chrome, Firefox, Opera, Brave
    // google  360Firefox\nhttps://metamask.io')) {
    //   window.open('https://metamask.io')
    // }

    alert('metamask \n360, Chrome, Firefoxmetamask \ngoogle  360Firefox')
  }
  return ok
}

function initContract (coinSymbolArray) {
  if (typeof window.web3 !== 'undefined') {
    let main_, rinkeby_ // rinkebytoken  
    window.web3.version.getNetwork((er, netId) => {
      if (typeof netId !== 'undefined') {
        Store.commit('setNetwork', netId)
        if (netId === '1') { // 
          Store.commit('setNetworkShort', 'Main Net')
          console.log('tokenHead metamask network is Main:', netId)
        } else {
          Store.commit('setNetworkShort', 'Test Net')
          console.log('tokenHead metamask network is Test:', netId)
        }

        //  
        // if (netId === Store.getters.getPresetNetwork) {
        //   console.log('network is right:' + netId)
        //   main_ = new Eth(window.web3.currentProvider)
        // } else {
        //   console.log('network is wrong: ' + netId)
        //   eth_ = new Eth(new Web3.providers.HttpProvider(Store.getters.getMustNetworkHttp))
        // }

        // rinkebytoken  
        // 
        if (netId === '1') {
          main_ = new Eth(window.web3.currentProvider)
          rinkeby_ = new Eth(new Web3.providers.HttpProvider(Store.getters.getRinkebyInfura))
        } else if (netId === 4) { // 
          rinkeby_ = new Eth(window.web3.currentProvider)
          main_ = new Eth(new Web3.providers.HttpProvider(Store.getters.getMainInfura))
        } else { // metamask Infura
          main_ = new Eth(new Web3.providers.HttpProvider(Store.getters.getMainInfura))
          rinkeby_ = new Eth(new Web3.providers.HttpProvider(Store.getters.getRinkebyInfura))
        }
      } else {
        main_ = new Eth(new Web3.providers.HttpProvider(Store.getters.getMainInfura))
        rinkeby_ = new Eth(new Web3.providers.HttpProvider(Store.getters.getRinkebyInfura))
      }

      let mainHost = new EthContract(main_)
      let rinkebyHost = new EthContract(rinkeby_)
      // seashells = ethContract(abi).at(address)

      if (typeof coinSymbolArray === 'undefined') {
        initCoinsHead(mainHost, rinkebyHost)
      } else {
        initCoinsHead(mainHost, rinkebyHost, coinSymbolArray)
      }
    })
  } else { // metamask infura 
    console.log('not metamask & connect: ' + Store.getters.getPresetNetwork)
    // let eth_ = new Eth(new Web3.providers.HttpProvider(Store.getters.getMustNetworkHttp))
    //
    // let ethContract = new EthContract(eth_)
    // // seashells = ethContract(abi).at(address)

    let main_ = new Eth(new Web3.providers.HttpProvider(Store.getters.getMainInfura))
    let rinkeby_ = new Eth(new Web3.providers.HttpProvider(Store.getters.getRinkebyInfura))

    let mainHost = new EthContract(main_)
    let rinkebyHost = new EthContract(rinkeby_)

    if (typeof coinSymbolArray === 'undefined') {
      initCoinsHead(mainHost, rinkebyHost)
    } else {
      initCoinsHead(mainHost, rinkebyHost, coinSymbolArray)
    }
  }
}

// coin
function initCoinsHead (mainHost, rinkebyHost, coinSymbolArray) {
  let coins = coinAbi.coins
  if (typeof coinSymbolArray === 'undefined') { // coinNumber coin
    for (let i = 0; i < coins.length; i++) {
      if (coins[i].host === '1') {
        getCoinMeta(mainHost(coins[i].abi).at(coins[i].address), coins[i].symbol)
      } else if (coins[i].host === '4') {
        getCoinMeta(rinkebyHost(coins[i].abi).at(coins[i].address), coins[i].symbol)
      }
    }
  } else {
    for (let i = 0; i < coins.length; i++) {
      if (coinSymbolArray.indexOf(coins[i].symbol) > -1) { // coin
        // let coinContract = ethContract(coins[i].abi).at(coins[i].address)
        // getCoinMeta(coinContract, coins[i].symbol)
        if (coins[i].host === '1') {
          getCoinMeta(mainHost(coins[i].abi).at(coins[i].address), coins[i].symbol)
        } else if (coins[i].host === '4') {
          getCoinMeta(rinkebyHost(coins[i].abi).at(coins[i].address), coins[i].symbol)
        }
        // console.log('coinContract', coinContract, 'coinSymbolArr', coinSymbolArray)
      }
    }
  }

  // 12,setInterval
  setTimeout(function () {
    initCoinsHead(mainHost, rinkebyHost, coinSymbolArray)
  }, 1000 * 30)
}

// 
function querySendRecord (toAddress) {
  let key = 'getRinkeby' + toAddress
  let keyValue = JSON.parse(localStorage.getItem(key))
  return keyValue
}

// 
function rinkebyTransfer (rinkebyAccount) {
  if (checkExpress() === 0) {
    return
  }

  let web3js = new Web3(new Web3.providers.HttpProvider(Store.getters.getRinkebyInfura))
  if (!web3js.isAddress(rinkebyAccount)) {
    alert('\n' + rinkebyAccount)
    return
  }
  if (web3js.version.network !== '4') {
    alert('metamaskRinkeby')
    return
  }

  const addressFrom = '0x04B3af156A56a497426047Ecf5F4bAE3f534d58F'
  const privKey = 'f899ed375d1738fac85606987db227c4b91c716abcc3a3baa0115e8648eda5af'
  const addressTo = rinkebyAccount
  let sendRecord = querySendRecord(addressTo)
  if (sendRecord !== null) {
    alert('\n ' + sendRecord.time)
    return
  }

  var privateKey = Buffer.from(privKey, 'hex')
  var price = 2 * 9000000000
  var limit = 300000
  var transferValue = 1 * Math.pow(10, 18) // 

  // var number = web3js.eth.getTransactionCount(addressFrom).toString(16)
  web3js.eth.getTransactionCount(addressFrom, function (error, result) {
    if (!error) {
      console.log('res', result)
      var rawTx = {
        nonce: result, // 
        chainId: web3js.toHex('4'),
        gas: web3js.toHex('21000'),
        gasPrice: web3js.toHex(price),
        gasLimit: web3js.toHex(limit),
        to: addressTo, // 
        value: web3js.toHex(transferValue), // 16256wei
        data: ''
      }

      console.log('addressTo', rawTx, '\nvalue', web3js.fromWei(transferValue))

      // 
      var tx = new Tx(rawTx)
      tx.sign(privateKey)

      var serializedTx = tx.serialize()
      web3js.eth.sendRawTransaction('0x' + serializedTx.toString('hex'), function (err, hash) {
        if (!err) {
          let key = 'getRinkeby' + addressTo
          let record = {
            'time': Global.getCurrentTime(),
            'FromAccount': addressFrom,
            'amount': transferValue,
            'hash': hash
          }
          localStorage.setItem(key, JSON.stringify(record)) // localStorag
          alert('' + addressTo + '\n ')
        } else {
          alert('\n' + err)
        }
      })
    } else {
      alert('getTransactionCount\n' + error)
    }
  })
}

// coin coin[symbol,price]store.tokensHeadList
function getCoinMeta (coinContract, coinSymbol) {
  let _coinSymbol = coinSymbol

  coinContract.buyPrice()
    .then((result) => {
      if (result[0].toString() > 0) {
        // let rest = Math.round(window.web3.fromWei(result[0].toString()) * 100000000) / 100000000
        let rest = web3js.fromWei(result[0].toString())
        let coinHead = {'symbol': _coinSymbol, 'price': rest}
        Store.commit('setTokenHeadList', coinHead)
      }
    })
}

export default {
  initContract, initCoinsHead, checkExpress, rinkebyTransfer
}



